import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";

import ReceivablePaymentUpdateForm from "./updateForm";

import notification from "../../../../components/notification";
// import { Input, Select, Divider } from "antd";
import HistoryReceivablePayment from "./history";

import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/financial/receivablePayment/actions";
import { Box } from "../../../App/app.style";
import { Spin, Tooltip } from "antd";
import Print from "./print";

const redirecTo = "/dashboard/financials/receivable-payments";

class UpdateReceivablePayment extends Component {
  state = {
    visible: false
  };

  componentDidMount() {
    const { editReceivablePayment } = this.props;
    const { id } = this.props.match.params;
    editReceivablePayment(id);
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.receivablePayment.error) {
      notification("error", this.props.receivablePayment.error);
      this.props.clearError();
    }
    if (this.props.receivablePayment.success) {
      // this.props.history.replace(
      //   redirecTo + "/update/" + this.props.receivablePayment.id_purchase_invoice
      // );
      //   this.props.history.replace(redirecTo);
      notification("success", this.props.receivablePayment.success);
      this.props.clearSuccess();
    }
    if (this.props.receivablePayment.warning) {
      // this.props.history.replace(
      //   redirecTo + "/update/" + this.props.receivablePayment.id_purchase_invoice
      // );
      //   this.props.history.replace(redirecTo);
      notification("warning", this.props.receivablePayment.warning);
      this.props.clearSuccess();
    }
  }

  handleModalHistory = () => {
    const { visible } = this.state;
    visible
      ? this.setState({ visible: false })
      : this.setState({ visible: true });
  };

  handleSubmit = value => {
    const { updateReceivablePayment } = this.props;
    const { id } = this.props.match.params;
    updateReceivablePayment(id, value);
  };

  handleSubmitCancel = value => {
    const { cancelReceivablePayment } = this.props;
    const { id } = this.props.match.params;

    cancelReceivablePayment(id, value);
  };

  render() {
    // console.log(this.props);
    const {
      loading,
      loadingForeign,
      loadingSubmit,
      receivablePayment,
      printData
    } = this.props.receivablePayment;
    const modified = {...receivablePayment, ...printData}

    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="receivablePayments.update.title" />}
            marginBottom={true}
          >
            <Tooltip placement="top" title="Info">
              <Button
                style={{
                  fontSize: "24px",
                  border: 0
                  // color: theme.palette.warning
                }}
                icon="issues-close"
                onClick={() => this.handleModalHistory()}
                loading={loading}
              >
                {/* <IntlMessages id="itemRequests.history" /> */}
              </Button>
            </Tooltip>

            <Print loading={loading} data={modified} />

            <Button
              style={{ marginLeft: "5px" }}
              onClick={() => this.props.history.replace(redirecTo)}
            >
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <HistoryReceivablePayment
            visible={this.state.visible}
            handleModalHistory={this.handleModalHistory}
            description={
              receivablePayment ? receivablePayment.description : null
            }
          />

          {loading && loadingForeign && !loadingSubmit ? (
            <div className="spinStyle">
              <Spin size="large" />
            </div>
          ) : (
            <ReceivablePaymentUpdateForm
              submit={this.handleSubmit}
              receivablePayment={this.props.receivablePayment}
              params={this.props.match.params}
              handleSubmitCancel={this.handleSubmitCancel}
            />
          )}
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    receivablePayment: state.receivablePayment,
    auth: state.auth
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(UpdateReceivablePayment);
